.container {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-around;
}

#container {
    background-color: red;
}
.upload {
    max-width: 450px;
    height: 500px;
    width: 100%;
    border: 1px solid #ddd;
    padding: 15px;
    margin: 20px;
    position: relative;
}

.file_up {
    position: relative;
    width: 100%;
    height: 100%;
    outline: none;

    &:before {
        content: '+';
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        background: white;
        color: rgb(250, 200, 107);
        font-size: 17rem;
        text-align: center;
        cursor: pointer;
        margin: auto;
    }
}

.file_img {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background: white;
}
.file_img img {
    width: 100%;
    height: 100%;
    display: block;
    object-fit: contain;
}
.file_img span {
    position: absolute;
    top: -13px;
    right: -13px;
    background: white;
    border: 1px solid #ddd;
    border-radius: 50%;
    padding: 6px 10px;
    cursor: pointer;
    font-weight: 900;
    color: crimson;
}

.container form {
    max-width: 500px;
    min-width: 290px;
    width: 100%;
    margin: 15px 30px;
}
.container form .row {
    width: 100%;
    margin: 15px 0;
}
.container form input,
textarea {
    width: 100%;
    min-height: 40px;
    padding: 0 5px;
}
.container form button {
    width: 200px;
    height: 40px;
    background: #555;
    color: white;
    text-transform: uppercase;
    letter-spacing: 2px;
    font-weight: 700;
}

.row {
    select,
    option {
        text-transform: capitalize;
    }
}
