@import 'normalize.css';
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700&display=swap');

:root {
    --default-textcolor: black;
    --default-textcolor-menu: #555;
}

* {
    box-sizing: border-box;
}

html {
    font-size: 62.5%;
}

body {
    font-family: 'Montserrat', sans-serif;
    font-size: 1.6rem;
    max-width: 1230px;
    min-height: 100vh;
    box-shadow: 0 0 35px #eee;
    margin: 0 auto;
    padding: 0 20px;
}

// CSS REACT SLICK
.slick-next .slick-prev {
    color: #555;
}

.slick-next {
    right: 0;
}

.slick-prev {
    left: 0;
}

* .slick-next:before,
.slick-prev:before {
    color: grey;
    font-size: 2.4rem;
}

@media (max-width: 720px) {
    .slick-next:before,
    .slick-prev:before {
        display: none;
    }
}
